import React from "react";
import { WingBlank, Button } from "antd-mobile";
import Step from "./Step";
import StyleWrapper from "./StyleWrapper";
import { Link } from "react-router-dom";

const StepContainer = ({ onSubmit,accountMoney,setting, isLogin,mobile }) => {
    return (
        <WingBlank>
            <StyleWrapper>
                <div className="top"></div>
                <div className="lists">
                    {isLogin ? (
                        <Step idx={1}> {mobile}</Step>
                    ) : (
                        <Step idx={1}>
                            <Link to="/register"></Link> 
                        </Step>
                    )}
                    {isLogin ? (
                        <Step idx={2}>
                            <span className="text-warnning">
                                {accountMoney}
                            </span>{" "}
                            <span className="text-warnning">
                                {setting[0]}
                            </span>
                        </Step>
                    ) : (
                        <Step idx={2}>
                            <Link to="/member/charge"></Link>  {setting[0]}
                        </Step>
                    )}

                    <Step idx={3}>
                         <span className="text-warnning">  {setting[1]}</span>
                    </Step>
                    <Step idx={4}>
                         <span className="text-warnning">  {setting[2]} </span> <span className="text-warnning"> {" "} 14:45{" "} </span>
                    </Step>
                    <Step idx={5}>
                         <span className="text-warnning"> 100% </span>
                    </Step>
                </div>

                <Button
                    type="warning"
                    style={{ backgroundColor: "#FF8E01" }}
                    onClick={() => {
                        onSubmit();
                    }}
                >
                    
                </Button>
            </StyleWrapper>
        </WingBlank>
    );
};

export default StepContainer;



